.b-check-list {
	padding-left: 5.6rem;
	&__item {
		position: relative;
		display: flex;
		font-size: 1.7rem;
		color: #293553;
		line-height: 1.8;
		margin-bottom: 2.8rem;
		&:last-child {
			margin-bottom: 0; }
		&:before {
			content: '';
			position: absolute;
			top: 1rem;
			left: -5.6rem;
			display: block;
			width: 3rem;
			height: 2rem;
			background: transparent url(data:image/png;base64,) center no-repeat;
			background-size: contain; } } }
