  right: 0;
  left: 0;
  width: 100%;
}

.c-page--background-accent {
  position: relative;
  background-color: #6b44ba;
  color: #ffffff;
}

.c-page--background-accent:before {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: transparent url("../assets/img/section-bg/item-3.png") left top no-repeat;
  background-size: auto;
  z-index: 0;
}

.c-main {
