.burger {
  position: relative;
  width: 32px;
  height: 32px;
  transition-duration: 0.5s;
  z-index: 101;
}

.burger:hover {
  cursor: pointer;
}

.burger__icon {
  position: absolute;
  top: 50%;
  left: 50%;
  height: 2px;
  width: 21px;
  transition-duration: 0.5s;
  background-color: #fff;
  -webkit-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

.burger__icon:before {
  transition-duration: 0.5s;
  position: absolute;
  top: -6px;
  left: 0;
  width: inherit;
  height: inherit;
  background-color: inherit;
  content: "";
}

.burger__icon:after {
  transition-duration: 0.5s;
  position: absolute;
  top: 6px;
  right: 0;
  width: 1em;
  height: inherit;
  background-color: inherit;
  content: "";
}

.burger.active .burger__icon {
  transition-duration: 0.5s;
  background: transparent;
}

.burger.active .burger__icon:before {
  -webkit-transform: rotateZ(45deg) scaleX(1) translate(4px, 4px);
  transform: rotateZ(45deg) scaleX(1.25) translate(4px, 4px);
  background-color: #fff;
}

.burger.active .burger__icon:after {
  -webkit-transform: rotateZ(-45deg) scaleX(1) translate(4px, -4px);
  transform: rotateZ(-45deg) scaleX(1.25) translate(4px, -4px);
  background-color: #fff;
  width: inherit;
}

@media screen and (min-width: 1025px) {
  .burger {
    width: 45px;
    height: 45px;
  }
  .burger__icon {
    width: 18px;
  }
}

.main-checkbox {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  -webkit-user-select: none;
  user-select: none;
  cursor: pointer;
  color: #293553;
  font-size: 1.4rem;
}

.main-checkbox__item {
  position: relative;
  display: inline-block;
  width: 1.6rem;
  height: 1.6rem;
  vertical-align: middle;
  background-color: #ffffff;
  border: 1px solid #293553;
  margin-right: .7rem;
  flex-shrink: 0;
  transition: all 0.2s ease;
  transform: translate3d(0, 0, 0);
  box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.05);
  border-radius: 0.2em;
  -webkit-border-radius: 0.2em;
  -moz-border-radius: 0.2em;
}

.main-checkbox__svg {
  position: absolute;
  top: 50%;
  left: 50%;
  fill: none;
  stroke: #fff;
  color: #ffffff;
  stroke-width: 2;
  stroke-linecap: round;
  stroke-linejoin: round;
  stroke-dasharray: 16px;
  stroke-dashoffset: 16px;
  transition: all 0.3s ease;
  transition-delay: 0.1s;
  transform: translate3d(-50%, -50%, 0);
  opacity: 0;
  width: 1rem;
}

.main-checkbox__check:checked + .main-checkbox__item {
  animation: wave 0.4s ease;
  background-color: #9560d9;
  border-color: #9560d9;
}

.main-checkbox__check:checked + .main-checkbox__item svg {
  stroke-dashoffset: 0;
  opacity: 1;
}

