}

.b-select {
  position: relative;
  width: 100%;
}

.b-select__header {
  position: relative;
  z-index: 1;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1.15rem 0;
  border-bottom: 1px solid #c3c3c3;
  background-color: #ffffff;
}

.b-select__header-val {
  overflow: hidden;
  white-space: nowrap;
  color: rgba(51, 51, 51, 0.4);
}

.b-select__header-val.is-selected {
  color: #293553;
}

.b-select__header-placeholder {
  color: rgba(51, 51, 51, 0.4);
}

.b-select__header-icon {
  position: absolute;
  right: 1rem;
  font-size: 1.8rem;
  color: rgba(51, 51, 51, 0.4);
  flex-shrink: 0;
  -webkit-transition: all 0.4s ease;
  -moz-transition: all 0.4s ease;
  -ms-transition: all 0.4s ease;
  -o-transition: all 0.4s ease;
  transition: all 0.4s ease;
}

.b-select__dropdown {
  position: absolute;
  top: calc(100% + .5rem);
  left: 0;
  right: 0;
  background-color: #ffffff;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.14);
  border-radius: 6px;
  -webkit-border-radius: 6px;
  -moz-border-radius: 6px;
  -webkit-transition: all 0.4s ease;
  -moz-transition: all 0.4s ease;
  -ms-transition: all 0.4s ease;
  -o-transition: all 0.4s ease;
  transition: all 0.4s ease;
  z-index: 6;
  opacity: 0;
  visibility: hidden;
  overflow: hidden;
}

.b-select__item {
  display: block;
  padding: 1.1rem 1.6rem;
  cursor: pointer;
  color: #293553;
}

.b-select__item:hover, .b-select__item:focus {
  background-color: #dbcff2;
}

.b-select__item.active {
  background-color: #dbcff2;
  color: #ffffff;
}

.b-select.active .b-select__header {
  box-shadow: 0px 1px 3px transparent;
  border-color: #9560d9;
}

.b-select.active .b-select__dropdown {
  opacity: 1;
  visibility: visible;
}

.b-select.active .b-select__header-icon {
  -webkit-transform: rotate(180deg);
  -moz-transform: rotate(180deg);
  -ms-transform: rotate(180deg);
  transform: rotate(180deg);
}
