.b-price-block {
	position: relative;
	box-shadow: 0 44px 45px rgba(157, 157, 157, 0.14);
	border-radius: 3px;
	border: 1px solid #ebeaee;
	background-color: #ffffff;
	margin-bottom: 2.5rem;
	&__header {
		padding: 3rem;
		height: 16rem;
		background: transparent url(data:image/png;base64,) center bottom no-repeat;
		background-size: cover;
		text-align: center;
		color: #192646; }

	&__caption {
		position: relative;
		font-size: 2.2rem;
		margin-bottom: 1.5rem;
		&:after {
			content: '';
			display: block;
			width: 4rem;
			height: .2rem;
			margin: 1rem auto 0;
			background-color: $color-accent; } }
	&__price {
		font-size: 2.8rem;
		font-weight: 500; }
	&__price-desc {
		font-size: 1.7rem;
		line-height: .5;
		font-weight: 500;
		opacity: .85; }
	&__body {
		padding: 1.6rem 2.6rem; }
	&__sub-caption {

		font-size: 1.1rem;
		text-transform: uppercase;
		color: rgba(25, 38, 70, 0.3);
		margin: 2.4rem 0 1rem; }
	&__footer {
		display: flex;
		justify-content: center;
		padding: 0rem 2.6rem 4rem; }

	&--accent {
		.b-price-block__header {
			color: #ffffff;
			background-image: url(data:image/png;base64,); } }

	@media screen and (min-width: $lg) {
		margin-bottom: 0; } }
